import React, {useState, useEffect} from "react";
import styled from "styled-components";

import MenuItem from './menu-item'

const MenuContainer = styled.aside`
  grid-area: menu;
  width: 260px;
  background-color: #313a46;
  border-right: 1px solid #e8ebed;
  box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
  z-index: 2;

  .logo-container { 
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 80px;
    margin-top: 16px;
    margin-bottom: 16px;
  }

`;

const Menu = (props) => {
  const { itens, path } = props;
  const [itensMenu, setItensMenu] = useState([]);

  useEffect(() => {
    const data = itens.map(item => path.indexOf(item.url) !== -1 ? {...item, selected: true} : item)    
    setItensMenu(data);
  }, [itens, path]);

  return (
    <MenuContainer>
      <div className='logo-container'>
        <img src={require('../assets/logo_branca.png')} height="80px" alt="SENAC Pernambuco" />
      </div>
      <ul className="nav flex-column">
        {itensMenu.map((item, index) => <MenuItem 
                                      key={index} 
                                      // selected={item.selected} 
                                      url={item.url} 
                                      text={item.text} 
                                      icon={item.icon} />)}
      </ul>
    </MenuContainer>
  );
};

export default Menu;
