import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Ajax from "./ajax";
import Toolbar from "./toolbar";
import PesquisaItemMXM from "./pesquisa-itemmxm";

const Form = styled.form`
  padding: 16px;
`;

const ManualDeReferenciaTabCategoria = (props) => {
  const { id, modoDeEdicao } = props;

  const [message, setMessage] = useState("");
  const [messageTitle, setMessageTitle] = useState("");
  const [deleteConfirmMessage, setDeleteConfirmMessage] = useState(false);
  const [itemParaDeletar, setItemParaDeletar] = useState(null);
  const [manual, setManual] = useState(null);

  const [redirecionarParaEdicao, setRedirecionarParaEdicao] = useState(false);
  const [idRedirecionar, setIdRedirecionar] = useState(null);

  const [fkManualDeReferencia, setFkManualDeReferencia] = useState(id);

  // Lista e Paginação
  const [registros, setRegistros] = useState([]);

  const [pagina, setPagina] = useState(1);
  const [tamanhoDaLista, setTamanhoDaLista] = useState(7);
  const [numeroDePaginas, setNumeroDePaginas] = useState(1);
  const [keyword, setKeyword] = useState("");

  const onLoadManual = async () => {
    await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${id}`
        )
        .then((response) => {
          const { data } = response;
          setManual(data);
          onLoadRegistros();
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
          window.$("#modalManualDeReferenciaTabCategoria").modal("show");
        });
  }

  const onLoadRegistros = async () => {
    await ajax.current
      .send(
        "GET",
        keyword
          ? `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia/search/?fkManualDeReferencia=${fkManualDeReferencia}&pagina=${pagina}&tamanho=${tamanhoDaLista}&keyword=${keyword}`
          : `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia?fkManualDeReferencia=${fkManualDeReferencia}&pagina=${pagina}&tamanho=${tamanhoDaLista}`
      )
      .then((response) => {
        const { data, paginacao } = response;
        // alert(JSON.stringify(response));
        if(data) {
          setRegistros(data);
        }else {
          setRegistros([]);
        }
        
        setPagina(paginacao.pagina);
        setTamanhoDaLista(paginacao.tamanho);
        setNumeroDePaginas(paginacao.numeroDePaginas);
      })
      .catch((e) => {
        // alert(JSON.stringify(e))
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modalManualDeReferenciaTabCategoria").modal("show");
      });
  };

  useEffect(() => {
    if (id) {
      onLoadManual();
    }
  }, [id, numeroDePaginas, pagina, tamanhoDaLista, keyword,]);

  const ajax = useRef(null);

  const onRedirecionarParaEdicao = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${idRedirecionar}/edit`;
  };

  const handleChangeKeyWord = (value) => {
    setKeyword(value);
  };

  const searchbox = (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <div className="form-group">
        <input
          type="text"
          className="form-control"
          placeholder="Pesquisa..."
          onChange={(e) => handleChangeKeyWord(e.target.value)}
        />
      </div>
      <button
        type="button"
        class="btn btn-primary"
        style={{
          marginLeft: 16,
          height: 30,
          width: 30,
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
        }}
        onClick={() =>
          (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/categoria/`)
        }
      >
        <span className="material-icons">add</span>
      </button>
    </div>
  );

  const onDeleteItemManualDeReferencia = (item) => {
    setItemParaDeletar(item);
    setDeleteConfirmMessage(true);
    setMessageTitle("Atenção");
    setMessage(
      <div>
        Você confirma a exclusão definitiva da categoria/disciplina <b>{item.NOME}</b>.
      </div>
    );

    window.$("#modalManualDeReferenciaTabCategoria").modal("show");
  };

  const onDeletarItem = async () => {
    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia/${itemParaDeletar.FK_MANUAL_DE_REFERENCIA}/${itemParaDeletar.ID}/delete/`
      )
      .then((response) => {
        const { message } = response;
        setItemParaDeletar(null);
        setDeleteConfirmMessage(false);
        setMessageTitle("Sucesso");

        let div = document.createElement("div");
        div.innerHTML = message;

        setMessage(
          <div>
            O item <b>{message}</b> foi exluido com sucesso.
          </div>
        );
        // window.$("#modalManualDeReferencia").modal("show");
        onLoadRegistros();
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modalManualDeReferenciaTabCategoria").modal("show");
      });
  };

  return (
    <Form>
      <Ajax ref={ajax} />
      <Toolbar
        title={manual ? `${manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 'Disciplinas' : 'Categoria'}` : ''}
        actions={searchbox}
      />
      <table className="table table-striped" style={{ borderTop: "none" }}>
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">{manual ? manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 'Disciplina' : 'Categoria' : ''}</th>
            {manual ? manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? <th scope="col">Período</th> : '' : ''}
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {registros.map((item, index) => (
            <tr key={index}>
              <th scope="row">{item.ID}</th>
              <td>{item.NOME}</td>
              {manual ? manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? <td>{item.PERIODO}</td> : '' : ''}
              <td style={{maxWidth: 50, width: 50}}>
                <button
                  type="button"
                  className="btn btn-outline-primary"
                  style={{
                    width: 28,
                    height: 28,
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  onClick={() =>
                    (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/categoria/${item.ID}`)
                  }
                >
                  <i className="material-icons" style={{ fontSize: 19 }}>
                    edit
                  </i>
                </button>
              </td>
              <td style={{maxWidth: 50, width: 50}}>
                <button
                  type="button"
                  className="btn btn-outline-danger"
                  style={{
                    width: 28,
                    height: 28,
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  onClick={() => onDeleteItemManualDeReferencia(item)}
                >
                  <i className="material-icons" style={{ fontSize: 19 }}>
                    delete
                  </i>
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {numeroDePaginas <= 10 ? (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={() => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {Array.from(Array(numeroDePaginas).keys()).map((i, index) => (
              <li
                key={index}
                className={i + 1 === pagina ? "page-item active" : "page-item"}
              >
                <a
                  className="page-link"
                  href="#"
                  onClick={() => setPagina(i + 1)}
                >
                  {i + 1}
                </a>
              </li>
            ))}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      ) : (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={pagina === 1 ? null : () => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {/* <li className={pagina === 1 ? "page-item disabled" : "page-item"} onClick={() => setPagina(pagina - 1)}>
            <a className="page-link" href="#" aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li> */}

            {Array.from(Array(5).keys()).map((i, index) => {
              if (pagina + i <= numeroDePaginas) {
                return (
                  <li
                    key={index}
                    className={
                      pagina + i === pagina ? "page-item active" : "page-item"
                    }
                  >
                    <a
                      className="page-link"
                      href="#"
                      onClick={() => setPagina(pagina + i)}
                    >
                      {pagina + i}
                    </a>
                  </li>
                );
              }
            })}

            {/* <li className={pagina === numeroDePaginas ? "page-item disabled" : "page-item"}>
            <a className="page-link" href="#" aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li> */}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      )}

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        data-backdrop="static"
        id="modalManualDeReferenciaTabCategoria"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              {deleteConfirmMessage ? (
                <div>
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                    style={{ marginRight: 16 }}
                  >
                    Cancelar
                  </button>
                  <button
                    type="button"
                    className="btn btn-danger"
                    // data-dismiss="modal"
                    onClick={onDeletarItem}
                  >
                    Excluir
                  </button>
                </div>
              ) : redirecionarParaEdicao ? (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={onRedirecionarParaEdicao}
                >
                  OK
                </button>
              ) : (
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  OK
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </Form>
  );
};

export default ManualDeReferenciaTabCategoria;
