import React, { useState, useRef, useEffect } from "react";
import Toolbar from "../components/toolbar";
import Ajax from "../components/ajax";

const ManualDeReferencia = (props) => {
  const ajax = useRef(null);

  const [messageTitle, setMessageTitle] = useState("");
  const [message, setMessage] = useState("");
  const [registros, setRegistros] = useState([]);

  const [pagina, setPagina] = useState(1);
  const [tamanhoDaLista, setTamanhoDaLista] = useState(7);
  const [numeroDePaginas, setNumeroDePaginas] = useState(1);
  const [keyword, setKeyword] = useState("");

  const handleChangeKeyWord = (value) => {
    setPagina(1);
    setKeyword(value);
  };

  useEffect(() => {
    async function load() {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia${
            keyword ? `/search` : ""
          }?pagina=${pagina}&tamanho=${tamanhoDaLista}${
            keyword ? `&keyword=${keyword}` : ``
          }`
        )
        .then((response) => {
          setRegistros(response.data);
          setTamanhoDaLista(response.paginacao.tamanho);
          setPagina(response.paginacao.pagina);
          setNumeroDePaginas(response.paginacao.numeroDePaginas);
        })
        .catch((e) => {
          // setMessageTitle("Atenção")
          // setMessage(e.message);
          // window.$('#modal').modal('show');
        });
    }

    load();
  }, [keyword, numeroDePaginas, pagina, tamanhoDaLista]);

  const searchbox = (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <div className="form-group">
        <input
          type="text"
          className="form-control"
          placeholder="Pesquisa..."
          onChange={(e) => handleChangeKeyWord(e.target.value)}
        />
      </div>
      <button
        type="button"
        class="btn btn-primary"
        style={{
          marginLeft: 16,
          height: 30,
          width: 30,
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
        }}
        onClick={() =>
          (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/cadastro`)
        }
      >
        <span className="material-icons">add</span>
      </button>
    </div>
  );

  return (
    <div>
      <Ajax ref={ajax} />
      <Toolbar title="Manuais de Referência" actions={searchbox} />
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Cód.</th>
            <th scope="col">Curso</th>
            <th scope="col">Ativo</th>
            <th scope="col">Tipo</th>
            <th scope="col">Criação</th>
            {/* <th scope="col">Modificação</th> */}
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {registros.map((item, index) => (
            <tr key={index}>
              <th scope="row">{item.ID}</th>
              <td>{item.FK_CURSO}</td>
              <td>{item.Curso.TITULO}</td>
              <td>
                <input type="checkbox" checked={item.ATIVO} />
              </td>
              <td>
                {item.Curso.TIPO}
              </td>
              <td>
                {item.CREATED ? new Date(item.CREATED).toLocaleString() : ""}
              </td>
              {/* <td>
                {item.MODIFIED ? new Date(item.MODIFIED).toLocaleString() : ""}
              </td> */}
              <td>
                <button
                  type="button"
                  class="btn btn-outline-primary"
                  style={{
                    width: 28,
                    height: 28,
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  onClick={() =>
                    (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${item.ID}/edit`)
                  }
                >
                  <i className="material-icons" style={{ fontSize: 19 }}>
                    edit
                  </i>
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {numeroDePaginas <= 10 ? (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={() => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {Array.from(Array(numeroDePaginas).keys()).map((i) => (
              <li
                className={i + 1 === pagina ? "page-item active" : "page-item"}
              >
                <a
                  className="page-link"
                  href="#"
                  onClick={() => setPagina(i + 1)}
                >
                  {i + 1}
                </a>
              </li>
            ))}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      ) : (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={pagina === 1 ? null : () => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {/* <li className={pagina === 1 ? "page-item disabled" : "page-item"} onClick={() => setPagina(pagina - 1)}>
          <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li> */}

            {Array.from(Array(5).keys()).map((i) => {
              if (pagina + i <= numeroDePaginas) {
                return (
                  <li
                    className={
                      pagina + i === pagina ? "page-item active" : "page-item"
                    }
                  >
                    <a
                      className="page-link"
                      href="#"
                      onClick={() => setPagina(pagina + i)}
                    >
                      {pagina + i}
                    </a>
                  </li>
                );
              }
            })}

            {/* <li className={pagina === numeroDePaginas ? "page-item disabled" : "page-item"}>
          <a class="page-link" href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li> */}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      )}

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        id="modal"
        data-backdrop="static"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-primary"
                data-dismiss="modal"
              >
                OK
              </button>
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </div>
  );
};

export default ManualDeReferencia;
