import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Ajax from "./ajax";

const SearchableContainer = styled.div`
  .horizontal-layout {
    display: flex;
    flex-direction: row;
  }

  button {
    max-height: 32px;
    margin-left: 8px;
  }

  .search-input-text {
    min-width: 100%;
    width: 100%;
    max-width: 100%;
  }

  .selected-row {
    background-color: #f45;
  }
`;

const PesquisaCurso = (props) => {
  const { disabled } = props;
  const ajax = useRef(null);
  const [itens, setItens] = useState([]);
  const [keyword, setKeyword] = useState("a");
  const [item, setItem] = useState({ id: 0 });
  const { value, onChange, onSelectItem } = props;

  const carregarItens = async (value) => {
    await ajax.current
      .send(
        "GET",
        `${process.env.REACT_APP_DOMAIN_API}/api/curso/search?keyword=${keyword}`
      )
      .then((response) => {
        setItens(response.data);
      })
      .catch((e) => {
        alert(JSON.stringify(e));
        // setMessageDialog(e.message);
        // window.$("#exampleModal").modal("toggle");
      });
  };

  const openSearchableDialog = async () => {
    await carregarItens("0");
    window.$("#exampleModalScrollable").modal("toggle");
  };

  const handlerChangeKeyword = (value) => {
    setKeyword(value);
    if (value) carregarItens(value);
    else carregarItens("0");
  };

  const onSalvar = () => {
    onSelectItem(item);
    window.$("#exampleModalScrollable").modal("toggle");
  };

  useEffect(() => {
    carregarItens();
  }, [keyword]);

  return (
    <SearchableContainer>
      <Ajax ref={ajax} />
      <div className="form-group">
        <label>Curso</label>
        <div className="horizontal-layout">
          <input
            type="text"
            className="form-control"
            aria-describedby="emailHelp"
            disabled={true}
            value={value}
            onChange={onChange}
          />
          {disabled ? (
            ""
          ) : (
            <button
              type="button"
              className="btn btn-light"
              onClick={openSearchableDialog}
            >
              <i className="material-icons">search</i>
            </button>
          )}
          {/* Modal */}
          <div
            className="modal fade"
            id="exampleModalScrollable"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalScrollableTitle"
            aria-hidden="true"
            data-backdrop="static"
          >
            <div
              className="modal-dialog modal-dialog-scrollable modal-lg"
              role="document"
            >
              <div className="modal-content">
                <div className="modal-header" style={{ maxHeight: 100 }}>
                  <h5 className="modal-title" id="exampleModalScrollableTitle">
                    <div>
                      <div className="form-group">
                        <label className="col-form-label">
                          Pesquisa de Cursos
                        </label>
                        <input
                          type="text"
                          className="form-control search-input-text"
                          id="recipient-name"
                          style={{ width: 450 }}
                          onChange={(e) => handlerChangeKeyword(e.target.value)}
                          placeholder="Curso... Código..."
                        />
                      </div>
                    </div>
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body" style={{ minHeight: 300 }}>
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <th scope="col">Código</th>
                        <th scope="col">Curso</th>
                        <th scope="col">Tipo</th>
                        <th scope="col"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {itens.map((i, index) => (
                        <tr
                          key={index}
                          style={{
                            backgroundColor:
                              item.CODIGO === i.CODIGO ? "#616161" : "",
                            color: item.CODIGO === i.CODIGO ? "#F5F5F5" : "",
                          }}
                        >
                          <td>{i.CODIGO}</td>
                          <td>{i.TITULO}</td>
                          <td>{i.TIPO}</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-info btn-sm"
                              onClick={() => setItem(i)}
                            >
                              Selecionar
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                <div className="modal-footer" style={{ maxHeight: 100 }}>
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                  >
                    Fechar
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary"
                    onClick={onSalvar}
                  >
                    Salvar
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* End Modal */}
        </div>
      </div>
    </SearchableContainer>
  );
};

export default PesquisaCurso;
