import React, { useState, useEffect, useRef } from 'react';
import Toolbar from '../components/toolbar';
import Ajax from '../components/ajax';

const MeuPerfil = (props) => {
  const { logged } = props;
  const id = logged.ID;
  const modoDeEdicao = id !== undefined;

  const [message, setMessage] = useState('');
  const [messageTitle, setMessageTitle] = useState('');

  const [nome, setNome] = useState('');
  const [sobrenome, setSobrenome] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [unidades, setUnidades] = useState([]);
  const [tiposFuncionario, setTiposFuncionario] = useState([]);
  const [fkUnidade, setFkUnidade] = useState(0);
  const [fkTipoFuncionario, setFkTipoFuncionario] = useState(0);
  const [ativo, setAtivo] = useState(false);
  const [created, setCreated] = useState(null);
  const [modified, setModified] = useState(null);

  const ajax = useRef(null);

  useEffect(() => {
    const carregarUnidades = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/unidade/`
        ).then(response => {
          setUnidades(response.data);
          carregarTipoFuncionario();
        }).catch(e => {
          setMessageTitle('Atenção');
          setMessage(e.message);
        });
    };


    const carregarTipoFuncionario = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/tipofuncionario/`
        ).then(response => {
          setTiposFuncionario(response);

        }).catch(e => {
          setMessageTitle('Atenção');
          setMessage(e.message);
        });
    };

    carregarUnidades();
    

    setNome(logged.NOME);
    setSobrenome(logged.SOBRENOME);
    setEmail(logged.EMAIL);
    setFkUnidade(logged.FK_UNIDADE);
    setFkTipoFuncionario(logged.FK_TIPOFUNCIONARIO);
    setAtivo(logged.ATIVO);
  }, []);

  const btAlterar = async () => {
    const params = {
      nome: nome,
      sobrenome: sobrenome,
      email: email,
      password: password,
      confirmPassword: confirmPassword,
      fkUnidade: fkUnidade,
      fkTipoFuncionario: fkTipoFuncionario,
      ativo,
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/authentication/update/`,
        params
      ).then(response => {
        setMessageTitle('Sucesso');
        setMessage(response.message);
        window.$('#modal').modal('show');
      }).catch(e => {
        setMessageTitle('Atenção');
        setMessage(e.message);
        window.$('#modal').modal('show');
      });
  };

  return (
    <div>
      <Ajax ref={ajax} />
      <Toolbar title="Meu Perfil" />
      <form>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label>Nome</label>
            <input type="text" className="form-control" value={nome} onChange={e => setNome(e.target.value)} />
          </div>
          <div className="form-group col-md-6">
            <label>Sobrenome</label>
            <input type="text" className="form-control" value={sobrenome} onChange={e => setSobrenome(e.target.value)} />
          </div>
        </div>
        <div className="form-group">
          <label>Email</label>
          <input type="email" className="form-control" value={email} onChange={e => setEmail(e.target.value)} disabled={modoDeEdicao ? true : false } />
        </div>
        <div className='form-row'>
          <div className="form-group col-md-6">
            <label>Senha</label>
            <input type="password" className="form-control" value={password} onChange={e => setPassword(e.target.value)} />
          </div>
          <div className="form-group col-md-6">
            <label>Confirmação de Senha</label>
            <input type="password" className="form-control" value={confirmPassword} onChange={e => setConfirmPassword(e.target.value)} />
          </div>
        </div>
        <div className='form-row'>
          <div className="form-group col-md-6">
            <label for="inputState">Unidade</label>
            <select id="inputState" className="form-control" value={fkUnidade} onChange={e => setFkUnidade(e.target.options[e.target.selectedIndex].value)} disabled>
              <option></option>
              {unidades.map((item, index) => <option value={item.ID} key={index}>{item.NOME}</option>)}
            </select>
          </div>
          <div className="form-group col-md-6">
            <label for="inputState">Tipo Funcionário</label>
            <select id="inputState" className="form-control" value={fkTipoFuncionario} onChange={e => setFkTipoFuncionario(e.target.options[e.target.selectedIndex].value)} disabled>
              <option></option>
              {tiposFuncionario.map((item, index) => <option value={item.ID} key={index}>{item.NOME}</option>)}
            </select>
          </div>
        </div>
        
        <div className="form-group">
          <div className="form-check">
            <input className="form-check-input" type="checkbox" checked={ativo} onChange={e => setAtivo(e.target.checked)} />
            <label className="form-check-label" for="gridCheck">
              Ativo
            </label>
          </div>
        </div>
        <div style={{display: 'flex', flexDirection: 'row'}}>
          <button type="button" className="btn btn-dark" onClick={() => window.history.back()}>Voltar</button>
          <div style={{flex: 1}}></div>
          <button type="button" className="btn btn-primary" onClick={btAlterar}>Alterar</button>
        </div>
      </form>


      {/* <!-- Modal --> */}
      <div className="modal fade" id="modal" tabIndex="-1" aria-labelledby="" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">{messageTitle}</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              {message}
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-primary" data-dismiss="modal">OK</button>
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </div>
  )
}

export default MeuPerfil;