import React, {useState, useEffect} from "react";
import styled from "styled-components";
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./components/header";
import Menu from "./components/menu";
import Content from "./components/content";

import routes from "./routes";
import Usuario from "./pages/usuario";
import UsuarioForm from "./pages/usuario-form";
import MeuPerfil from "./pages/meu-perfil";
import ManualDeReferencia from "./pages/manualDeReferencia";
import ManualDeReferenciaForm from "./pages/manualDeReferencia-form";
import ManualDeReferenciaItemForm from "./pages/manual-de-referencia-item-form";
import ManualDeReferenciaItemCategoriaForm from './pages/manual-de-referencia-item-categoria-form';

const MasterPageContainer = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: grid;
  grid-template-areas:
    "menu header header header header"
    "menu content content content content"
    "menu content content content content";
  }
`;

const Masterpage = (props) => {
  const { logged } = props;
  const [menuItens, setMenuItens] = useState([]);


  useEffect(() => {
    setMenuItens([
      logged.TipoFuncionario.NOME === "Administrador" || logged.TipoFuncionario.NOME === "Manual De Referência" ? { text: 'Manuais', icon: 'fact_check', selected: false, url: `${process.env.REACT_APP_DOMAIN}/manualdereferencia` } : {},
      logged.TipoFuncionario.NOME === "Administrador" ? { text: 'Usuários', icon: 'people', selected: false, url: `${process.env.REACT_APP_DOMAIN}/usuario` } : {},
      // { text: 'Configurações', icon: 'settings', selected: false, url: '/configuracoes' },
    ])
  }, [])


  return (
    <MasterPageContainer>
        <Header logged={logged} />
        <Menu itens={menuItens} path={props.location.pathname} />
        <Content>
          <Switch>  
            <Route exact path="/meuperfil" render={(props) => <MeuPerfil {...props} logged={logged} />} />
            <Route exact path="/manualdereferencia/:id/item/:iditem" render={(props) => <ManualDeReferenciaItemForm {...props} logged={logged} />} />
            <Route exact path="/manualdereferencia/:id/item/" render={(props) => <ManualDeReferenciaItemForm {...props} logged={logged} />} />
            
            <Route exact path="/manualdereferencia/:id/categoria/:iditem" render={(props) => <ManualDeReferenciaItemCategoriaForm {...props} logged={logged} />} />
            <Route exact path="/manualdereferencia/:id/categoria/" render={(props) => <ManualDeReferenciaItemCategoriaForm {...props} logged={logged} />} />
            
            <Route exact path="/manualdereferencia/:id/edit" render={(props) => <ManualDeReferenciaForm {...props} logged={logged} />} />
            <Route exact path="/manualdereferencia/cadastro" render={(props) => <ManualDeReferenciaForm {...props} logged={logged} />} />
            <Route exact path="/manualdereferencia" render={(props) => <ManualDeReferencia {...props} logged={logged} />} />
            <Route exact path="/usuario/:id/edit" render={(props) => <UsuarioForm {...props} logged={logged} />} />
            <Route exact path="/usuario/cadastro" render={(props) => <UsuarioForm {...props} logged={logged} />} />
            <Route exact path="/usuario" render={(props) => <Usuario {...props} logged={logged} />} />
            
            

              {/* {routes.map((route, index) => (
                // Render more <Route>s with the same paths as
                // above, but different components this time.
                <Route
                  key={index}
                  path={route.path}
                  exact={route.exact}
                  render={(p) => <route.main {...p} logged={props.logged} />}
                />
              ))} */}
            </Switch>
        </Content>
      </MasterPageContainer>
  );
};

export default Masterpage;

