import React from "react";
import styled from "styled-components";

const HeaderContainer = styled.header`
  grid-area: header;
  grid-column-start: 1;
  margin-left: 260px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 64px;
  background-color: #ffffff;
  box-shadow: 0 0 30px 0 rgba(82, 63, 105, 0.05);
  border-bottom: 1px solid #e8ebed;
  z-index: 2;
  color: #212121;

  div:nth-child(2) {
    flex: 1;
    margin-left: 16px;
    margin-right: 16px;
    font-weight: bold;
  }
`;

const Header = (props) => {
  const { logged } = props;

  const onClickSair = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/logout`;
  }

  return (
    <HeaderContainer>
      <div>
        <button type="button" className="btn btn-light">
          <svg
            width="16px"
            height="16px"
            viewBox="0 0 16 16"
            className="bi bi-list"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M2.5 11.5A.5.5 0 0 1 3 11h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 7h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 3h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"
            />
          </svg>
        </button>
      </div>
      <div>{process.env.REACT_APP_NAME}</div>
      <div>
      <div className="btn-group">
        <button type="button" className="btn btn-dark dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          <span style={{marginRight: 16}}>{`${logged.NOME} ${logged.SOBRENOME}`}</span>
          <svg width="16px" height="16px" viewBox="0 0 16 16" className="bi bi-person-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
            <path d="M13.468 12.37C12.758 11.226 11.195 10 8 10s-4.757 1.225-5.468 2.37A6.987 6.987 0 0 0 8 15a6.987 6.987 0 0 0 5.468-2.63z"/>
            <path fillRule="evenodd" d="M8 9a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
            <path fillRule="evenodd" d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zM0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8z"/>
          </svg>
        </button>
        <div className="dropdown-menu dropdown-menu-right">
          <button className="dropdown-item" type="button" onClick={() => window.location.href = `${process.env.REACT_APP_DOMAIN}/meuperfil`}>Meu Perfil</button>
          <button className="dropdown-item" type="button" onClick={onClickSair}>Sair</button>
        </div>
      </div>
      </div>
    </HeaderContainer>
  );
};

export default Header;
