import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Ajax from "./ajax";
import PesquisaCurso from "./pesquisa-curso";

const Form = styled.form`
  padding: 16px;
`;

const ManualDeReferenciaTabCurso = (props) => {
  const { id, modoDeEdicao } = props;

  const [message, setMessage] = useState("");
  const [messageTitle, setMessageTitle] = useState("");

  const [redirecionarParaEdicao, setRedirecionarParaEdicao] = useState(false);
  const [idRedirecionar, setIdRedirecionar] = useState(null);
  const [fkTipoManualDeReferencia, setFkTipoManualDeReferencia] = useState(null);
  const [curso, setCurso] = useState("");
  const [cursoObj, setCursoObj] = useState(null);
  const [fkCurso, setFkCurso] = useState(0);
  const [descricao, setDescricao] = useState("");
  const [ativo, setAtivo] = useState(false);
  const [created, setCreated] = useState(null);
  const [modified, setModified] = useState(null);
  const [tipoManualDeReferencia, setTipoManualDeReferencia] = useState([]);

  useEffect(() => {
    const onLoadTipoDeManual = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/tipomanualdereferencia/`
        )
        .then((response) => {
          const { data } = response;
          setTipoManualDeReferencia(data);

          if (id) {
            onLoadManualDeReferencia();
          }
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
          window.$("#modal").modal("show");
        });
    };

    const onLoadManualDeReferencia = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${id}`
        )
        .then((response) => {
          const { data } = response;
          setFkCurso(data.FK_CURSO);
          setCurso(`${data.Curso.CODIGO} - ${data.Curso.TITULO}`);
          setCursoObj(data.Curso);
          setDescricao(data.DESCRICAO);
          setAtivo(data.ATIVO);
          setFkTipoManualDeReferencia(data.FK_TIPO_MANUAL_DE_REFERENCIA);
          setCreated(data.CREATED);
          setModified(data.MODIFIED);
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
          window.$("#modal").modal("show");
        });
    };

    onLoadTipoDeManual();
  }, [id]);

  const ajax = useRef(null);

  const btCadastrar = async () => {
    const params = {
      fkCurso: fkCurso,
      descricao: descricao,
      ativo: ativo,
      fkTipoManualDeReferencia
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/`,
        params
      )
      .then((response) => {
        const { data } = response;
        setMessageTitle("Sucesso");
        setMessage(response.message);
        setIdRedirecionar(data.ID);
        window.$("#modal").modal("show");
        setRedirecionarParaEdicao(true);
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const btAlterar = async () => {
    const params = {
      fkCurso: fkCurso,
      descricao: descricao,
      ativo: ativo,
      fkTipoManualDeReferencia
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${id}/edit`,
        params
      )
      .then((response) => {
        setMessageTitle("Sucesso");
        setMessage(response.message);
        window.$("#modal").modal("show");
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const onSelectCurso = (e) => {
    setCurso(`${e.CODIGO} - ${e.TITULO}`);
    setFkCurso(e.CODIGO);
    setCursoObj(e);
  };

  const onRedirecionarParaEdicao = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${idRedirecionar}/edit`;
  };

  return (
    <Form>
      <Ajax ref={ajax} />
      <div class="form-group">
        <label>Tipo de Manual</label>
        <select class="form-control" value={fkTipoManualDeReferencia} onChange={e => setFkTipoManualDeReferencia(parseInt(e.target.options[e.target.selectedIndex].value))}>
          <option></option>
          {tipoManualDeReferencia.map((item, index) => <option value={item.ID}>{item.NOME}</option>)}
        </select>
      </div>

      {fkTipoManualDeReferencia === 1 ? <div><PesquisaCurso
        title="Curso"
        value={curso}
        onChange={(e) => setCurso(e.target.value)}
        onSelectItem={onSelectCurso}
        disabled={modoDeEdicao}
      />

      <div className="row"> 
        <div className="col">
            <div class="form-group">
              <label for="exampleInputEmail1">Eixo</label>
              <input
                type="text"
                class="form-control"
                disabled
                value={`${cursoObj ? cursoObj.EIXO : ''}`}
              />
            </div>
          </div>
          <div className="col">
            <div class="form-group">
              <label for="exampleInputEmail1">Segmento</label>
              <input
                type="text"
                class="form-control"
                disabled
                value={`${cursoObj ? cursoObj.SEGMENTO : ''}`}
              />
            </div>
          </div>
          <div className="col">
            <div class="form-group">
              <label for="exampleInputEmail1">Tipo</label>
              <input
                type="text"
                class="form-control"
                disabled
                value={`${cursoObj ? cursoObj.TIPO : ''}`}
              />
            </div>
          </div>
      </div></div> : '' }

      <div className="form-group">
        <label>Descrição</label>
        <textarea
          className="form-control"
          id="exampleFormControlTextarea1"
          rows="3"
          value={descricao}
          onChange={(e) => setDescricao(e.target.value)}
        ></textarea>
      </div>

      <div className="form-group">
        <div className="form-check">
          <input
            className="form-check-input"
            type="checkbox"
            checked={ativo}
            onChange={(e) => setAtivo(e.target.checked)}
          />
          <label className="form-check-label">Ativo</label>
        </div>
      </div>
      <div style={{ display: "flex", flexDirection: "row" }}>
        <button
          type="button"
          className="btn btn-dark"
          onClick={() =>
            (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/`)
          }
        >
          Voltar
        </button>
        <div style={{ flex: 1 }}></div>
        <button
          type="button"
          className="btn btn-primary"
          onClick={modoDeEdicao ? btAlterar : btCadastrar}
        >
          {modoDeEdicao ? "Alterar" : "Cadastrar"}
        </button>
      </div>

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        data-backdrop="static"
        id="modal"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              {redirecionarParaEdicao ? (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={onRedirecionarParaEdicao}
                >
                  OK
                </button>
              ) : (
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  OK
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </Form>
  );
};

export default ManualDeReferenciaTabCurso;
