import React, { useState, useEffect, useRef } from "react";
import Toolbar from "../components/toolbar";
import Ajax from "../components/ajax";

const ManualDeReferenciaItemCategoriaForm = (props) => {
  const { id, iditem } = props.match.params;
  const modoDeEdicao = iditem !== undefined;

  const [message, setMessage] = useState("");
  const [messageTitle, setMessageTitle] = useState("");
  const [redirecionarParaEdicao, setRedirecionarParaEdicao] = useState(false);
  const [fkManualDeReferencia, setFkManualDeReferencia] = useState(id);
  const [registro, setRegistro] = useState(null);
  
  const [manual, setManual] = useState(null);
  const [categoriaDisciplina, setCategoriaDisciplina] = useState('');
  const [periodo, setPeriodo] = useState('');

  const ajax = useRef(null);


  useEffect(() => {
    const carregarManual = async () => {
      
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${fkManualDeReferencia}`
        )
        .then((response) => {
          const { data } = response;
          setManual(data);

          if (iditem) {
            carregarRegistro();
          }
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    const carregarRegistro = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia/${fkManualDeReferencia}/${iditem}`
        )
        .then((response) => {
          const { data } = response;
          setCategoriaDisciplina(data.NOME);
          setPeriodo(data.PERIODO);
        })
        .catch((e) => {
          alert(JSON.stringify(e))
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    if(id) {
      carregarManual();
    }

  }, []);


  const btCadastrar = async () => {
    const params = {
      NOME: categoriaDisciplina,
      PERIODO: periodo,
      FK_MANUAL_DE_REFERENCIA: fkManualDeReferencia
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia/`,
        params
      )
      .then((response) => {
        const { data } = response;


        // setRedirecionarParaEdicao(true);

        setMessageTitle("Sucesso");
        setMessage(response.message);
        window.$("#modal").modal("show");

        setCategoriaDisciplina('');
        setPeriodo('');
        // onLoadRegistros();
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const btAlterar = async () => {
    const params = {
      NOME: categoriaDisciplina,
      PERIODO: periodo,
      FK_MANUAL_DE_REFERENCIA: fkManualDeReferencia
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia/${fkManualDeReferencia}/${iditem}/edit`,
        params
      )
      .then((response) => {
        const { data } = response;

        setMessageTitle("Sucesso");
        setMessage(response.message);
        window.$("#modal").modal("show");
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const onRedirecionarParaEdicao = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${registro.FK_MANUAL_DE_REFERENCIA}/edit`;
  };

  return (
    <div>
      <Ajax ref={ajax} />
      <Toolbar title={manual ? `${manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 'Disciplina' : 'Categoria'}` : ``} />
      <form>
        <div>
          <div class="form-group">
            <label for="exampleInputEmail1">{manual ? `${manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 'Disciplina' : 'Categoria'}` : ``}</label>
            <input
              type="text"
              class="form-control"
              value={categoriaDisciplina}
              onChange={(e) => setCategoriaDisciplina(e.target.value)}
            />
          </div>

          {manual ? 
            manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? <div class="form-group">
            <label for="exampleInputEmail1">Período</label>
            <input
              type="number"
              class="form-control"
              value={periodo}
              onChange={(e) => setPeriodo(e.target.value)}
            />
          </div> : ''
          : ''}

          
        </div>

        <div style={{ display: "flex", flexDirection: "row" }}>
          <button
            type="button"
            className="btn btn-dark"
            onClick={() =>
              (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/edit/`)
            }
          >
            Voltar
          </button>
          <div style={{ flex: 1 }}></div>
          <button
            type="button"
            className="btn btn-primary"
            onClick={modoDeEdicao ? btAlterar : btCadastrar}
          >
            {modoDeEdicao ? "Alterar" : "Cadastrar"}
          </button>
        </div>
      </form>

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        id="modal"
        data-backdrop="static"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              {redirecionarParaEdicao ? (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={onRedirecionarParaEdicao}
                >
                  Voltar para Manual de Referência
                </button>
              ) : (
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  OK
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </div>
  );
};

export default ManualDeReferenciaItemCategoriaForm;
