import React from 'react';
import styled from 'styled-components';

const ToolbarContainer = styled.div`
  display: flex;
  flex-direction:  row;
  align-items: center;
  height: 48px;
  margin-bottom: 16px;
  color: #212121;

  div:nth-child(1) {
    font-size: 13px;
    font-weight: bold;
    flex: 1;
  }
`;

const Toolbar = (props) => {
  const {title, actions} = props;

  return (
    <ToolbarContainer>
      <div>
        <h5>{title}</h5>
      </div>
      <div>{actions}</div>
    </ToolbarContainer>
  )
}

export default Toolbar;