import React from "react";
import styled from "styled-components";

const LoaderContainer = styled.div`
  .loader-container {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    animation-name: loaderContainerIn;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;
    z-index: 2000;
  }

  @keyframes loaderContainerIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    opacity: 0.3;
  }

  .loader {
    z-index: 11;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .loader div:nth-child(1),
  .loader div:nth-child(2),
  .loader div:nth-child(3) {
    position: absolute;
    width: 50px;
    height: 50px;
    background-color: #1bc5bd;
    border-radius: 50%;
    opacity: 0;

    animation-name: loadAnimation;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
  }

  .loader div:nth-child(2) {
    animation-delay: 0.4s;
  }

  .loader div:nth-child(3) {
    animation-delay: 0.8s;
  }

  @keyframes loadAnimation {
    from {
      width: 50px;
      height: 50px;
      opacity: 0;
    }
    50% {
      opacity: 0.1;
    }
    to {
      width: 150px;
      height: 150px;
      opacity: 0;
    }
  }
`;

const Loader = () => {
  return (
    <LoaderContainer>
      <div className="loader-container">
        <div className="background"></div>
        <div className="loader">
          <div></div>
          <div></div>
          <div></div>
        </div>
      </div>
    </LoaderContainer>
  );
};

export default Loader;
