import React, { useState, useEffect, useRef } from "react";
import Toolbar from "../components/toolbar";
import Ajax from "../components/ajax";
import PesquisaItemMXM from "../components/pesquisa-itemmxm";

import {
  maskCPF,
  maskTelefone,
  removeMask,
  format,
  unFormart,
  replaceValue,
} from "../utils/mask.utils";

const ManualDeReferenciaItemForm = (props) => {
  const { id, iditem } = props.match.params;
  const modoDeEdicao = iditem !== undefined;

  const [message, setMessage] = useState("");
  const [messageTitle, setMessageTitle] = useState("");
  const [redirecionarParaEdicao, setRedirecionarParaEdicao] = useState(false);
  const [registro, setRegistro] = useState({});

  const [descricao, setDescricao] = useState("");
  const [especificacaoTecnica, setEspecificacaoTecnica] = useState("");
  const [unidade, setUnidade] = useState("");
  const [marcasReferencia, setMascasReferencia] = useState("");
  const [quantidade, setQuantidade] = useState(0);
  const [codigoMXM, setCodigoMXM] = useState("");
  const [fkManualDeReferencia, setFkManualDeReferencia] = useState(id);
  const [fkItemModulo, setFkItemModulo] = useState(null);
  const [manual, setManual] = useState(null);
  const [ultimoValorDeCompra, setUltimoValorDeCompra] = useState(null);
  const [fkCategoriaDisciplina, setFkCategoriaDisciplina] = useState(null);
  const [categoriasDisciplinas, setCategoriasDisciplinas] = useState([]);
  const [itemModulo, setItemModulo] = useState([]);
  const [created, setCreated] = useState("");
  const [modified, setModified] = useState("");
  const [mapaDeCotacao, setMapaDeCotacao] = useState('');

  const ajax = useRef(null);

  useEffect(() => {
    const carregarManual = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${fkManualDeReferencia}`
        )
        .then((response) => {
          const { data } = response;
          setManual(data);

          carregarCategoriaDisciplina();

        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    const carregarCategoriaDisciplina = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/categoriaitemmanualdereferencia?fkManualDeReferencia=${fkManualDeReferencia}&pagina=${1}&tamanho=${10000}`
        )
        .then((response) => {
          const { data } = response;
          setCategoriasDisciplinas(data);

          carregarItemModulo();
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    const carregarItemModulo = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/itemmodulo/`
        )
        .then((response) => {
          const { data } = response;
          setItemModulo(data);

          if (iditem) {
            carregarRegistro();
          }

        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    const carregarRegistro = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia/${fkManualDeReferencia}/${iditem}`
        )
        .then((response) => {
          const { data } = response;

          setDescricao(data.DESCRICAO);
          setEspecificacaoTecnica(data.ESPECIFICACAO_TECNICA);
          setUnidade(data.UNIDADE);
          setMascasReferencia(data.MARCAS_DE_REFERENCIA);
          setQuantidade(data.QUANTIDADE);
          setCodigoMXM(data.CODIGO_MXM);
          setFkManualDeReferencia(data.FK_MANUAL_DE_REFERENCIA);
          setUltimoValorDeCompra(data.ULTIMO_VALOR_DE_COMPRA);
          setFkCategoriaDisciplina(data.FK_CATEGORIA_ITEM_MANUAL_DE_REFERENCIA);
          setFkItemModulo(data.FK_ITEM_MODULO);
          setMapaDeCotacao(data.MAPA_DE_COTACAO);
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    carregarManual();
  }, []);

  const onSelectCurso = (e) => {
    setCodigoMXM(e.CODIGO);
    setDescricao(e.DESCRICAO);
    setUltimoValorDeCompra(e.VALOR);
    setEspecificacaoTecnica(e.ESPECIFICACAO_TECNICA);
    setMascasReferencia(e.MARCA_REFERENCIA);
    setUnidade(e.UNIDADE);
    setMapaDeCotacao(e.MAPA_DE_COTACAO);
  };

  const btCadastrar = async () => {
    const params = {
      DESCRICAO: descricao,
      ESPECIFICACAO_TECNICA: especificacaoTecnica,
      UNIDADE: unidade,
      MARCAS_DE_REFERENCIA: marcasReferencia,
      QUANTIDADE: quantidade,
      CODIGO_MXM: codigoMXM,
      FK_MANUAL_DE_REFERENCIA: fkManualDeReferencia,
      ULTIMO_VALOR_DE_COMPRA: ultimoValorDeCompra,
      FK_CATEGORIA_ITEM_MANUAL_DE_REFERENCIA: fkCategoriaDisciplina,
      FK_ITEM_MODULO: fkItemModulo,
      MAPA_DE_COTACAO: mapaDeCotacao
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia/`,
        params
      )
      .then((response) => {
        const { data } = response;

        setRedirecionarParaEdicao(true);
        setRegistro(data);

        setMessageTitle("Sucesso");
        setMessage(response.message);
        window.$("#modal").modal("show");

        setDescricao("");
        setEspecificacaoTecnica("");
        setUnidade("");
        setMascasReferencia("");
        setQuantidade(0);
        setCodigoMXM("");
        setUltimoValorDeCompra(null);
        setMapaDeCotacao('');
        // onLoadRegistros();
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const btAlterar = async () => {
    const params = {
      DESCRICAO: descricao,
      ESPECIFICACAO_TECNICA: especificacaoTecnica,
      UNIDADE: unidade,
      MARCAS_DE_REFERENCIA: marcasReferencia,
      QUANTIDADE: quantidade,
      CODIGO_MXM: codigoMXM,
      FK_MANUAL_DE_REFERENCIA: fkManualDeReferencia,
      ULTIMO_VALOR_DE_COMPRA: ultimoValorDeCompra,
      FK_ITEM_MODULO: fkItemModulo,
      MAPA_DE_COTACAO: mapaDeCotacao
    };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia/${fkManualDeReferencia}/${iditem}/edit`,
        params
      )
      .then((response) => {
        const { data } = response;

        setMessageTitle("Sucesso");
        setMessage(response.message);
        window.$("#modal").modal("show");

        // setDescricao("");
        // setEspecificacaoTecnica("");
        // setUnidade("");
        // setMascasReferencia("");
        // setQuantidade(0);
        // setCodigoMXM("");
        // setUltimoValorDeCompra(null);
        // onLoadRegistros();
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modal").modal("show");
      });
  };

  const onRedirecionarParaEdicao = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${registro.FK_MANUAL_DE_REFERENCIA}/edit`;
  };

  return (
    <div>
      <Ajax ref={ajax} />
      <Toolbar title="Novo Item" />
      <form>
        {manual ? manual.Curso.TIPO === 'Graduação' || manual.Curso.TIPO === 'Pós-Graduação' ? 
          <div class="form-group">
            <label>{manual ? manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 'Disciplina' : 'Categoria' : ''}</label>
            <select class="form-control" value={fkCategoriaDisciplina} onChange={e => setFkCategoriaDisciplina(parseInt(e.target.options[e.target.selectedIndex].value))}>
              <option></option>
              {categoriasDisciplinas.map((item, index) => <option value={item.ID}>{item.NOME}</option>)}
            </select>
          </div>
        : '' : ''}

          <div className="form-group">
            <label for="inputState">Módulo</label>
            <select id="inputState" className="form-control" value={fkItemModulo} onChange={e => setFkItemModulo(e.target.options[e.target.selectedIndex].value)}>
              <option></option>
              {itemModulo.map((item, index) => <option value={item.ID} key={index}>{item.NOME}</option>)}
            </select>
          </div>

        <PesquisaItemMXM
          title="Item MXM"
          value={`${codigoMXM} - ${descricao}`}
          onChange={(e) => alert(e.target.value)}
          onSelectItem={onSelectCurso}
          // disabled={modoDeEdicao}
        />

        <div class="form-group">
          <label>Especificação Técnica</label>
          <textarea
            class="form-control"
            rows="3"
            disabled
            value={especificacaoTecnica}
            onChange={(e) => setEspecificacaoTecnica(e.target.value)}
          ></textarea>
        </div>

        <div>
          <div className="row">
            <div className="col">
              <div class="form-group">
                <label for="exampleInputEmail1">Unidade</label>
                <input
                  type="text"
                  class="form-control"
                  value={unidade}
                  disabled
                  onChange={(e) => setUnidade(e.target.value)}
                />
              </div>
            </div>
            {/* <div className="col">
              <div class="form-group">
                <label for="exampleInputEmail1">Marca(s) de Referência</label>
                <input
                  type="text"
                  class="form-control"
                  value={marcasReferencia}
                  onChange={(e) => setMascasReferencia(e.target.value)}
                />
              </div>
            </div> */}
            <div className="col">
              <div class="form-group">
                <label for="exampleInputEmail1">Quantidade</label>
                <input
                  type="number"
                  class="form-control"
                  value={quantidade}
                  onChange={(e) => setQuantidade(e.target.value)}
                />
              </div>
            </div>
            <div className="col">
              <div class="form-group">
                <label for="exampleInputEmail1">Mapa de Cotação</label>
                <input
                  type="number"
                  class="form-control"
                  value={mapaDeCotacao}
                  disabled
                  // onChange={(e) => setQuantidade(e.target.value)}
                />
              </div>
            </div>
            <div className="col">
              <div class="form-group">
                <label for="exampleInputEmail1">Valor</label>
                <input
                  type="text"
                  class="form-control"
                  value={ultimoValorDeCompra ? format(ultimoValorDeCompra) : format(0)}
                  // disabled
                  onChange={(e) => setUltimoValorDeCompra(replaceValue(unFormart(e.target.value)))}
                />
              </div>
            </div>
          </div>
          {/* <div className="row" style={{ marginTop: 16 }}>
            <div className="col">
              <button
                type="button"
                className="btn btn-primary"
                onClick={btCadastrar}
              >
                Cadastrar
              </button>
            </div>
          </div> */}
        </div>

        <div style={{ display: "flex", flexDirection: "row" }}>
          <button
            type="button"
            className="btn btn-dark"
            onClick={() =>
              (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/edit/`)
            }
          >
            Voltar
          </button>
          <div style={{ flex: 1 }}></div>
          <button
            type="button"
            className="btn btn-primary"
            onClick={modoDeEdicao ? btAlterar : btCadastrar}
          >
            {modoDeEdicao ? "Alterar" : "Cadastrar"}
          </button>
        </div>
      </form>

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        id="modal"
        data-backdrop="static"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              {redirecionarParaEdicao ? (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={onRedirecionarParaEdicao}
                >
                  Voltar para Manual de Referência
                </button>
              ) : (
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  OK
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </div>
  );
};

export default ManualDeReferenciaItemForm;
