import React from 'react';

import styled from 'styled-components';

const MenuItemContainer = styled.li`
  a {
    display: flex;
    text-decoration: none;
    height: 40px;
    align-items: center;
    color: rgb(108,117,125);
  }

  a .menu-container-text {
    flex: 1;
    margin-left: 16px;
    margin-right: 16px;
  }

  // a: hover {
  //   background-color: #1bc5bd;
  //   color: #C0FAEF;
  // }

  a.selected {
    background-color: #1bc5bd;
    color: #C0FAEF;
  }
`;

const MenuItem = (props) => {
  const {url, icon, text, selected, onClick} = props;

  return (
    <MenuItemContainer className="nav-item">
      <a className={url ? selected ? "nav-link selected" : "nav-link" : selected ? "nav-link disabled selected" : "nav-link disabled"} href={url} onClick={onClick}>
        <div className='menu-container-icon'>
          <span className='material-icons'>{icon}</span>
        </div>
        <div className='menu-container-text'>{text}</div>
      </a>
    </MenuItemContainer>
  )
}

export default MenuItem;