import React, { useRef, useState, useEffect } from "react";
import styled from "styled-components";

import isAutenticated from "../utils/isAuthenticated";

import Ajax from "../components/ajax";

const LoginContainer = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-image: url("${require("../assets/login_background.jpg")}");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  form {
    display: flex;
    flex-direction: column;
    width: 350px;
    background-color: #fff;
    padding: 24px;
    border-radius: 8px;
    box-shadow: 0px 3px 15px -11px #424242;
  }

  form .logo-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
  }

  form .title-system {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 16px;
    color: #424242;
  }

  form footer {
    margin-top: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    color: #757575;
  }

  form footer i {
    font-size: 11px;
  }
`;

const Login = (props) => {
  const ajax = useRef(null);

  const [autenticated, setAutenticated] = useState(false);

  useEffect(() => {
    isAutenticated().then((_) => {
      setAutenticated(_.logged);
    });
  });

  /**
   * Defining the login page state.
   */
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [messageDialog, setMessageDialog] = useState("");

  /**
   * event click button to sign on application
   */
  const onClickEntrarButton = async () => {
    const params = { email: email, password: password };

    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/authentication/`,
        params
      )
      .then((data) => {
        if (typeof data.token !== "undefined") {
          document.cookie = `_token_mf=${data.token}`;
          window.location.href = `${process.env.REACT_APP_DOMAIN}/`;
        }
      })
      .catch((e) => {
        setMessageDialog(e.message);
        window.$("#exampleModal").modal("toggle");
      });
  };

  const onKeyUpEvent = (e) => {
    if (e.keyCode === 13) {
      onClickEntrarButton();
    }
  };

  if (autenticated) {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/`;
    // return <Redirect to="/" />;
  }


  /**
   * Component Render
   */
  return (
    <LoginContainer>
      <Ajax ref={ajax} />
      <form onKeyUp={onKeyUpEvent}>
        <div className="logo-container">
          <img
            src={require("../assets/senac_logo.png")}
            height="64px"
            alt="#"
          />
        </div>
        <div className="title-system">
          <h4>{process.env.REACT_APP_NAME}</h4>
        </div>
        <div className="form-group">
          <label>Email</label>
          <input
            type="email"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            onChange={(e) => setEmail(e.target.value)}
          />
          <small id="emailHelp" className="form-text text-muted">
            Você deverá utilizar o email institucional.
          </small>
        </div>
        <div className="form-group">
          <label>Senha</label>
          <input
            type="password"
            className="form-control"
            id="exampleInputPassword1"
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <button
          type="button"
          className="btn btn-primary"
          onClick={onClickEntrarButton}
        >
          Entrar
        </button>
        <footer>
          ©2020 
        </footer>

        {/* Start Dialog  */}
        <div
          className="modal fade"
          id="exampleModal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
          data-backdrop="static"
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Atenção
                </h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">{messageDialog}</div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  Fechar
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* End Dialog  */}
      </form>
    </LoginContainer>
  );
};

export default Login;
