import React from "react";
import styled from "styled-components";

const FooterContainer = styled.footer`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: rgb(243, 246, 249);
  max-height: 16px;
  min-height: 16px;
  height: 16px;
  font-size: 11px;
  font-weight: bold;
  color: rgb(108, 117, 125);
  margin-top: 16px;
`;

const Footer = () => {
  return (
    <FooterContainer>
      <div>Senac Pernambuco ©2020</div>
    </FooterContainer>
  );
};

export default Footer;
