import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Ajax from "./ajax";
import Toolbar from "./toolbar";
// import PesquisaItemMXM from "./pesquisa-itemmxm";




const Form = styled.form`
  padding: 16px;
`;

const ManualDeReferenciaTabItens = (props) => {
  const { id, modoDeEdicao } = props;

  const [message, setMessage] = useState("");
  const [messageTitle, setMessageTitle] = useState("");
  const [deleteConfirmMessage, setDeleteConfirmMessage] = useState(false);
  const [itemParaDeletar, setItemParaDeletar] = useState(null);

  const [redirecionarParaEdicao, setRedirecionarParaEdicao] = useState(false);
  const [idRedirecionar, setIdRedirecionar] = useState(null);

  const [fkManualDeReferencia, setFkManualDeReferencia] = useState(id);

  const [valorTotal, setValorTotal] = useState(0);

  // Lista e Paginação
  const [registros, setRegistros] = useState([]);
  const [manual, setManual] = useState(null);

  const [pagina, setPagina] = useState(1);
  const [tamanhoDaLista, setTamanhoDaLista] = useState(7);
  const [numeroDePaginas, setNumeroDePaginas] = useState(1);
  const [keyword, setKeyword] = useState("");
  
  const [modulos, setModulos] = useState([]);
  const [selectedModulo, setSelectedModulo] = useState(null);

  const onLoadRegistros = async () => {
    try{
      await ajax.current
      .send(
        "GET",
        keyword
          ? `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia/search/?fkManualDeReferencia=${fkManualDeReferencia}&pagina=${pagina}&tamanho=${tamanhoDaLista}&keyword=${keyword}&modulo=${selectedModulo}`
          : `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia?fkManualDeReferencia=${fkManualDeReferencia}&pagina=${pagina}&tamanho=${tamanhoDaLista}&modulo=${selectedModulo}`
      )
      .then((response) => {
        const { data, paginacao, valorTotal } = response;
        // alert(JSON.stringify(response));
        // console.log(response);

        
        let size = data.length;
        for (let index = 1; index <= size; index++) {
          const sequence = ((pagina - 1) * tamanhoDaLista) + index;
          data[index-1] = { ...data[index-1], SEQUENCE: sequence };
        }

        setRegistros(data);
        // alert(JSON.stringify(data[0].SEQUENCE));

        setPagina(paginacao.pagina);
        setTamanhoDaLista(paginacao.tamanho);
        setNumeroDePaginas(paginacao.numeroDePaginas);
        setValorTotal(valorTotal);
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modalManualDeReferencia").modal("show");
      });
    }
    catch(err) {
      setRegistros([])
    }
  };

  let ajax = useRef(null);

  useEffect(() => {
    const carregarManual = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${fkManualDeReferencia}`
        )
        .then((response) => {
          const { data } = response;
          setManual(data);
  
          onLoadRegistros();
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };

    const carregarModulo = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/itemmodulo/`
        )
        .then((response) => {
          const { data } = response;
          setModulos(data);
  
          carregarManual();
        })
        .catch((e) => {
          setMessageTitle("Atenção");
          setMessage(e.message);
        });
    };
  

    if (id) {
      carregarModulo();
    }
  }, [id, pagina, keyword, selectedModulo]);

  const onRedirecionarParaEdicao = () => {
    window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${idRedirecionar}/edit`;
  };

  const handleChangeKeyWord = (value) => {
    setKeyword(value);
  };


  const searchbox = (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <div className="form-group col-md-6">
            {/* <label for="inputState">Módulo</label> */}
            <select id="inputState" className="form-control" value={selectedModulo} onChange={e => setSelectedModulo(e.target.options[e.target.selectedIndex].value)}>
              <option value='null'>Módulo</option>
              {modulos.map((item, index) => <option value={item.ID} key={index}>{item.NOME}</option>)}
            </select>
          </div>

      <div className="form-group">
        <input
          type="text"
          className="form-control"
          placeholder="Pesquisa..."
          onChange={(e) => handleChangeKeyWord(e.target.value)}
        />
      </div>
      <button
        type="button"
        class="btn btn-primary"
        style={{
          marginLeft: 16,
          height: 30,
          width: 30,
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
        }}
        onClick={() =>
          (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/item/`)
        }
      >
        <span className="material-icons">add</span>
      </button>
    </div>
  );

  const onDeleteItemManualDeReferencia = (item) => {
    setItemParaDeletar(item);
    setDeleteConfirmMessage(true);
    setMessageTitle("Atenção");
    setMessage(
      <div>
        Você confirma a exclusão definitiva do item <b>{item.DESCRICAO}</b>.
      </div>
    );

    window.$("#modalManualDeReferencia").modal("show");
  };

  const onDeletarItem = async () => {
    await ajax.current
      .send(
        "POST",
        `${process.env.REACT_APP_DOMAIN_API}/api/itemmanualdereferencia/${itemParaDeletar.FK_MANUAL_DE_REFERENCIA}/${itemParaDeletar.ID}/delete/`
      )
      .then((response) => {
        const { message } = response;
        setItemParaDeletar(null);
        setDeleteConfirmMessage(false);
        setMessageTitle("Sucesso");

        let div = document.createElement("div");
        div.innerHTML = message;

        setMessage(
          <div>
            O item <b>{message}</b> foi exluido com sucesso.
          </div>
        );
        // window.$("#modalManualDeReferencia").modal("show");
        onLoadRegistros();
      })
      .catch((e) => {
        setMessageTitle("Atenção");
        setMessage(e.message);
        window.$("#modalManualDeReferencia").modal("show");
      });
  };


  return (
    <Form>
      <Ajax ref={ajax} />
      <Toolbar
        title={
          <h4>
            Total: {`R$ ${parseFloat(valorTotal).toFixed(2)}`}
          </h4>
        }
        actions={searchbox}
      />
      <table className="table table-striped" style={{ borderTop: "none" }}>
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Cód.</th>
            <th scope="col">Descrição</th>
            {/* <th scope="col">Especificação Técnica</th> */}
            <th scope="col">Unidade</th>
            {/* <th scope="col">Marca(s) de Referência</th> */}
            <th scope="col">Quantidade</th>
            {manual ? manual.Curso.TIPO === 'Graduação' || manual.Curso.TIPO === 'Pós Graduação' ? <th>Disciplina</th> : '' : ''}
            <th scope="col">Valor</th>
            <th scope="col">Valor Total</th>
            <th scope="col">Módulo</th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>

        {registros ? <tbody>
          {registros.map((item, index) => (
            <tr key={index}>
              <th scope="row">{item.SEQUENCE}</th>
              <td>{item.CODIGO_MXM}</td>
              <td>{item.DESCRICAO}</td>
              {/* <td>{item.ESPECIFICACAO_TECNICA}</td> */}
              <td>{item.UNIDADE}</td>
              {/* <td>{item.MARCAS_DE_REFERENCIA}</td> */}
              <td>{item.QUANTIDADE}</td>
              {manual ? manual.Curso.TIPO === 'Graduação' || manual.Curso.TIPO === 'Pós Graduação' ? <td>{item.CategoriaItemManualDeReferencium.NOME}</td> : '' : ''}
              <td>R$ {parseFloat(item.ULTIMO_VALOR_DE_COMPRA).toFixed(2)}</td>
              <td>R$ {parseFloat(item.VALOR_TOTAL).toFixed(2)}</td>
              <td>{item.ItemModulo.NOME}</td>
              <td style={{maxWidth: 50, width: 50}}>
                <button
                  type="button"
                  className="btn btn-outline-primary"
                  style={{
                    width: 28,
                    height: 28,
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  onClick={() =>
                    (window.location.href = `${process.env.REACT_APP_DOMAIN}/manualdereferencia/${fkManualDeReferencia}/item/${item.ID}`)
                  }
                >
                  <i className="material-icons" style={{ fontSize: 19 }}>
                    edit
                  </i>
                </button>
              </td>
              <td style={{maxWidth: 50, width: 50}}>
                <button
                  type="button"
                  className="btn btn-outline-danger"
                  style={{
                    width: 28,
                    height: 28,
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  onClick={() => onDeleteItemManualDeReferencia(item)}
                >
                  <i className="material-icons" style={{ fontSize: 19 }}>
                    delete
                  </i>
                </button>
              </td>
            </tr>
          ))}
        </tbody> : ''}
      </table>

      {numeroDePaginas <= 10 ? (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={() => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {Array.from(Array(numeroDePaginas).keys()).map((i, index) => (
              <li
                key={index}
                className={i + 1 === pagina ? "page-item active" : "page-item"}
              >
                <a
                  className="page-link"
                  href="#"
                  onClick={() => setPagina(i + 1)}
                >
                  {i + 1}
                </a>
              </li>
            ))}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      ) : (
        <nav aria-label="...">
          <ul className="pagination  justify-content-end">
            <li
              className={pagina === 1 ? "page-item disabled" : "page-item"}
              onClick={pagina === 1 ? null : () => setPagina(pagina - 1)}
            >
              <a
                className="page-link"
                href="#"
                tabIndex="-1"
                aria-disabled="true"
              >
                Anterior
              </a>
            </li>
            {/* <li className={pagina === 1 ? "page-item disabled" : "page-item"} onClick={() => setPagina(pagina - 1)}>
            <a className="page-link" href="#" aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li> */}

            {Array.from(Array(5).keys()).map((i, index) => {
              if (pagina + i <= numeroDePaginas) {
                return (
                  <li
                    key={index}
                    className={
                      pagina + i === pagina ? "page-item active" : "page-item"
                    }
                  >
                    <a
                      className="page-link"
                      href="#"
                      onClick={() => setPagina(pagina + i)}
                    >
                      {pagina + i}
                    </a>
                  </li>
                );
              }
            })}

            {/* <li className={pagina === numeroDePaginas ? "page-item disabled" : "page-item"}>
            <a className="page-link" href="#" aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li> */}
            <li
              className={
                pagina === numeroDePaginas ? "page-item disabled" : "page-item"
              }
            >
              <a
                className="page-link"
                href="#"
                onClick={() => setPagina(pagina + 1)}
              >
                Próximo
              </a>
            </li>
          </ul>
        </nav>
      )}

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        data-backdrop="static"
        id="modalManualDeReferencia"
        tabIndex="-1"
        aria-labelledby=""
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="">
                {messageTitle}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              {deleteConfirmMessage ? (
                <div>
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                    style={{ marginRight: 16 }}
                  >
                    Cancelar
                  </button>
                  <button
                    type="button"
                    className="btn btn-danger"
                    // data-dismiss="modal"
                    onClick={onDeletarItem}
                  >
                    Excluir
                  </button>
                </div>
              ) : redirecionarParaEdicao ? (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={onRedirecionarParaEdicao}
                >
                  OK
                </button>
              ) : (
                <button
                  type="button"
                  className="btn btn-primary"
                  data-dismiss="modal"
                >
                  OK
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- Modal --> */}
    </Form>
  );
};

export default ManualDeReferenciaTabItens;
