import React, { useState, useEffect, useRef } from "react";
import Toolbar from "../components/toolbar";
import Ajax from "../components/ajax";
import ManualDeReferenciaTabCurso from "../components/manual-de-referencia-tab-curso";
import ManualDeReferenciaTabItens from "../components/manual-de-referencia-tab-itens";
import ManualDeReferenciaTabCategoria from "../components/manual-de-referencia-tab-categoria";


const ManualDeReferenciaForm = (props) => {
  const { id } = props.match.params;
  const modoDeEdicao = id !== undefined;
  const [manual, setManual] = useState(null);

  useEffect(() => {
    const loadManual = async () => {
      await ajax.current
        .send(
          "GET",
          `${process.env.REACT_APP_DOMAIN_API}/api/manualdereferencia/${id}`
        )
        .then((response) => {
          const { data } = response;
          setManual(data);
        })
        .catch((e) => {
          // setMessageTitle("Atenção");
          // setMessage(e.message);
          // window.$("#modalManualDeReferenciaTabCategoria").modal("show");
        });
    }

    if(id){
      loadManual();
    }
  }, [])

  const ajax = useRef(null);

  const onClickDownloadExcel = async () => {
    var win = window.open(`${process.env.REACT_APP_DOMAIN_API}/api/relatorio/${id}`, '_blank');
    win.focus();
  }

  const buttonDownloadExcel = <button type="button" class="btn btn-light btn-sm" onClick={onClickDownloadExcel}>
    <i className='material-icons'>download</i>
  </button>

  return (
    <div>
      <Toolbar title="Cadastro de Manual de Referência" actions={[buttonDownloadExcel]} />
      <Ajax ref={ajax} />
      <ul className="nav nav-tabs" id="myTab" role="tablist">
        <li className="nav-item" role="presentation">
          <a
            className="nav-link active"
            id="manual-tab"
            data-toggle="tab"
            href="#manual"
            role="tab"
            aria-controls="manual"
            aria-selected="true"
          >
            Manual
          </a>
        </li>
        {manual ? manual.FK_TIPO_MANUAL_DE_REFERENCIA === 1 ? 
          manual.Curso.TIPO === 'Graduação' || manual.Curso.TIPO === 'Pós-Graduação' ? <li className="nav-item" role="presentation">
          <a
            className={modoDeEdicao ? "nav-link" : "nav-link disabled"}
            id="categoria-tab"
            data-toggle="tab"
            href="#categoria"
            role="tab"
            aria-controls="categoria"
            aria-selected="false"
          >
            Categorias/Disciplinas
          </a>
        </li> : ''
        : '' : ''}
        
        <li className="nav-item" role="presentation">
          <a
            className={modoDeEdicao ? "nav-link" : "nav-link disabled"}
            id="item-tab"
            data-toggle="tab"
            href="#item"
            role="tab"
            aria-controls="item"
            aria-selected="false"
          >
            Itens
          </a>
        </li>
      </ul>
      <div className="tab-content" id="myTabContent">
        <div
          className="tab-pane fade show active"
          id="manual"
          role="tabpanel"
          aria-labelledby="manual-tab"
        >
          <div style={{ borderLeft: "1px solid #dee2e6" }}>
            <ManualDeReferenciaTabCurso
              id={id}
              modoDeEdicao={modoDeEdicao}
              match={props.match}
            />
          </div>
        </div>
        <div
          className="tab-pane"
          id="categoria"
          role="tabpanel"
          aria-labelledby="categoria-tab"
        >
          <div style={{ borderLeft: "1px solid #dee2e6" }}>
            <ManualDeReferenciaTabCategoria
              id={id}
              modoDeEdicao={modoDeEdicao}
              match={props.match}
            />
          </div>
        </div>
        <div
          className="tab-pane fade"
          id="item"
          role="tabpanel"
          aria-labelledby="item-tab"
        >
          <div style={{ borderLeft: "1px solid #dee2e6" }}>
            <ManualDeReferenciaTabItens
              id={id}
              modoDeEdicao={modoDeEdicao}
              match={props.match}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManualDeReferenciaForm;
